import React, { Component } from "react";
import styled from "@emotion/styled";
import { BrowserRouter as Router, Route } from "react-router-dom";
import HomePage from "./Components/HomePage";
import Projects from "./Components/Projects/Projects";
import Contact from "./Components/Contact";
import About from "./Components/About";
import { GlobalSyles } from "./constants/styling";
import NavBar from "./Components/NavBar";
import Footer from "./Components/Footer";

const Container = styled.div`
  color: #212121;
  background-color: #f7f7f7;
  display: grid;
  min-height: 100vh;
  grid-template-rows: 80px minmax(calc(100vh - 130px), auto) 50px;
  grid-template-areas:
    "header"
    "main"
    "footer";
  header {
    grid-area: header;
  }
  .main {
    grid-area: main;
  }
  footer {
    grid-area: footer;
  }
`;
class App extends Component {
  render() {
    return (
      <React.Fragment>
        <GlobalSyles />
        <Router>
          <Container>
            <NavBar />
            <div className="main">
              <Route name="home" exact path="/" component={HomePage} />
              <Route name="projects" exact path="/projects" component={Projects} />
              <Route name="about" exact path="/about" component={About} />
              <Route name="contact" exact path="/contact" component={Contact} />
            </div>
            <Footer />
          </Container>
        </Router>
      </React.Fragment>
    );
  }
}

export default App;
