import React, { Component } from "react";
import styled from "@emotion/styled";
import Project from "./Project";
import projects from "./projects.json";
const ProjectsContainer = styled.div`
  padding: 40px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  justify-content: center;
  grid-gap: 20px;
  grid-area: main;
`;
class Projects extends Component {
  compareProject(a, b) {
    if (a.title < b.title) {
      return -1;
    }
    if (a.title > b.title) {
      return 1;
    }
    return 0;
  }

  render() {
    var projectArr = [];
    Object.keys(projects).forEach(function(project) {
      projectArr.push(projects[project]);
    });
    projectArr.sort(this.compareProject);
    return (
      <ProjectsContainer>
        {projectArr.map(project => (
          <Project className="project" key={project.title} title={project.title} description={project.description} url={project.url} github_url={project.github_url} />
        ))}
      </ProjectsContainer>
    );
  }
}
export default Projects;
